/*** New animation **/
/** Background size -10% to fix blink issue in RTL*/
@keyframes hyperlink-animate-in {
    0% {
        background-size: 0 2px;
        background-position-x: -10%;
    }

    100% {
        background-size: 100% 2px;
        background-position-x: -10%;
    }
}

/** Background size 110% to fix blink issue */
@keyframes hyperlink-animate-out {
    0% {
        background-size: 100% 2px;
        background-position-x: 110%;
    }
    90% {
        background-size: 0px 2px;
        background-position-x: 110%;
    }
    100% {
        background-size: 0px 2px;
        background-position-x: 110%;
    }
}

/*External button*/
/* KEYFRAMES */
    
@keyframes external-link-dash {
    0% {
      stroke-dashoffset: 100;
      rotate: 240deg;
    }
    63% {
      rotate: 0deg;
    }
    65% {
      stroke-dashoffset: 64;
    }
    65.01% {
      stroke-dashoffset: 0;
    }
    90% {
      stroke-dashoffset: -28;
    }
    100% {
      stroke-dashoffset: -36;
      rotate: 0deg;
    }
  }
  
  @keyframes external-link-dash2 {
    0% {
      stroke-dashoffset: 100;
    }
    100% {
      stroke-dashoffset: 37;
    }
  }
  
  @keyframes external-link-dash2-out {
    0% {
      stroke-dashoffset: 0;
    }
    100% {
      stroke-dashoffset: -63;
    }
  }
  
  @keyframes external-link-arrow {
    0% {
      opacity: 0;
    }
    55% {
      opacity: 0;
      transform: translate(35%, 20%);
    }
    60% {
      opacity: 1;
    }
    70% {
      transform: translate(60%, -5%);
    }
    90% {
      transform: matrix(1,0,0,1,16.009551,1.519303321838379);
    }
    100% {
      transform: matrix(1,0,0,1,16.009551,1.519303321838379);
      opacity: 1;
    }
  }

// For RTL button
@keyframes external-link-arrow-rtl {
    0% {
        opacity: 0;
    }

    55% {
        opacity: 0;
        transform: translate(-35%, 20%);
    }

    60% {
        opacity: 1;
    }

    70% {
        transform: translate(-60%, -5%);
    }

    90% {
        transform: matrix(1, 0, 0, 1, -16.009551, 1.519303321838379);
    }

    100% {
        transform: matrix(1, 0, 0, 1, -16.009551, 1.519303321838379);
        opacity: 1;
    }
}

  @keyframes external-link-arrow-out {
    0% {
      transform: matrix(1,0,0,1,16.009551,1.519303321838379);
      opacity: 1;
    }
    50% {
      transform: translate(60%, -5%);
    }
    70% {
      opacity: 1;
    }
    100% {
      transform: translate(35%, 20%);
      opacity: 0;
    }
  }

@keyframes glide-arrow-button-1-animation {
    0% {
        left: 50%;
    }
    100% {
      left: 32px;
      margin-left: 0;
    }
}

@keyframes glide-arrow-button-2-animation {
    0% {
        left: -6px;
    }
    100% {
      left: 50%;
    }
}

@keyframes glide-arrow-prev-button-1-animation {
    0% {
        left: 50%;
    }
    100% {
      left: -32px;
      margin-left: 0;
    }
}

@keyframes glide-arrow-prev-button-2-animation {
    0% {
        left: 40px;
    }
    100% {
      left: 50%;
    }
}

.bg-gradient-animate,
.mobius {
    .background {
        &:not(.showing-video) {
            &::after {
                display: none;
            }
        }
    }
}

.link-tertiary-on-hover {
    @include link-tertiary-on-hover;
}

.translate-x-with-opacity {
    &.trigger-animation {
        .translate-animation-item {
            opacity: 1;
            transform: translateX(0);
            @for $i from 1 through 50 {
                &:nth-of-type(#{$i}) {
                    transition-delay: #{$i * 0.04}s;
                }
            }
        }
    }
    .translate-animation-item {
        @include transition;
        opacity: 0;
        transform: translateX(3rem);
        @for $i from 1 through 50 {
            &:nth-of-type(#{$i}) {
                transition-delay: 0s;
            }
        }
    }
}



.translate-y-with-opacity {
    &.trigger-animation {
        .translate-animation-item {
            opacity: 1;
            transform: translateY(0);
            @for $i from 1 through 50 {
                &:nth-of-type(#{$i}) {
                    transition-delay: #{$i * 0.035}s;
                }
            }
            &.slow {
                transform: translateY(0);
                @for $i from 1 through 50 {
                    &:nth-of-type(#{$i}) {
                        transition-delay: #{$i * 0.06}s;
                    }
                }
            }
        }
    }
    .translate-animation-item {
        @include transition;
        opacity: 0;
        transform: translateY(-1.5rem);
        &.slow {
            transform: translateY(-3rem);
        }
        @for $i from 1 through 50 {
            &:nth-of-type(#{$i}) {
                transition-delay: 0s;
            }
        }
    }
}
