#pardot-form {
    .error {
        &.no-label {
            margin: $margin-default*0.25 0 $margin-default*0.75;
            color: var(--form-validation-error-color, $color-dark-coral);
            @include gt-body-text('small');
        }
        &.form-field {
            margin-bottom: 0;
            input[type="text"],
            input[type="email"],
            textarea,
            select {
                border-color: var(--form-validation-error-color, $color-dark-coral);
            }
        }
        label {
            color: var(--form-validation-error-color, $color-dark-coral);
        }
    }
    .errors {
        padding: $padding-default*0.25;
        margin: 0 0 $margin-default*0.75;
        background-color: var(--form-validation-error-color, $color-dark-coral);
    }
}