#pardot-form {
    .submit {
        display: inline-block;
        margin: 0;
        @include btn-vi-secondary-l1;
        padding: 0;
        width: auto;

        @include media-to-tablet {
            width: auto;
        }
    }
    input[type="submit"] {
        appearance: none;
        border: none;
        background: none;
        padding: 1.5rem 4.6rem;
        margin: 0;
        cursor: pointer;
        font-size: inherit;
        font-family: inherit;
        color: inherit;
    }
}

@import "../../generic/animations";